<template>
  <v-card elevation="1" >
    <v-card-title class="pa-0">
    <v-tabs v-model="tab">
      <v-tab @click="today">Today</v-tab>
      <v-tab @click="month">This month</v-tab>
      <v-tab @click="dialog = true" >Date picker</v-tab>
    <v-spacer></v-spacer>
      <v-tab disabled class="black--text">{{ dateString  }}</v-tab>
    </v-tabs>

    
    </v-card-title>
    <v-dialog v-model="dialog" max-width="650">
      <v-card >
        <v-date-picker v-model="dates" @input="range" range full-width></v-date-picker>
      </v-card>
    </v-dialog>
  </v-card>
</template>
<script>
 
import utility from './../../utility'
export default {
  name: 'DatePicker',
  props: {
    value: { type: Array, default: () => utility.currentDateRange}
  },
 
  data() {
 
    return {
       dates: utility.currentDateRange,
       dialog: false,
       tab: 1
    }
   
  },
 
  computed: {
    dateString() {
      if (this.value[0] == this.value[1])
        return this.value[0]
      else 
        return this.value.join(' ~ ')
    }
  },
  methods: {
    range () {
      if (this.dates.length > 1) {
        this.dialog = false
        this.$emit('input', this.dates)
      }
    },
    today () {
       this.$emit('input', utility.todayString)
    },
    month () {
       this.$emit('input', utility.currentDateRange)

    }
  }
  
  
}
</script>
 